import { PipelinesContext, PipelineStatus } from '@jumoo/processing';
import { css, customElement, html } from '@umbraco-cms/backoffice/external/lit';
import { UmbLitElement } from '@umbraco-cms/backoffice/lit-element';

@customElement('usync-background-workspace-element')
export class uSyncBackgroundWorkspaceElement extends UmbLitElement {
	#pipelinesContext: PipelinesContext;

	constructor() {
		super();

		this.#pipelinesContext = new PipelinesContext(this);
		console.debug('uSyncBackgroundWorkspaceElement', this.#pipelinesContext);
	}

	render() {
		return html`<umb-body-layout
			><div class="layout">
				<uui-box .headline=${this.localize.term('uSyncBackground_title')}>
					${this.localize.term('uSyncBackground_intro')}
				</uui-box>

				<usync-process-list
					.defaultStatus=${PipelineStatus.WAITING}
					.headline=${this.localize.term(
						'uSyncBackground_processes',
					)}></usync-process-list>

				<usync-pending-list
					.headline=${this.localize.term('uSyncBackground_queue')}></usync-pending-list>
			</div>
		</umb-body-layout>`;
	}

	static styles = css`
		.layout {
			display: flex;
			flex-direction: column;
			gap: var(--uui-size-space-4);
		}
	`;
}

export default uSyncBackgroundWorkspaceElement;
